import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Table, Button } from 'antd';
import { BtnSave } from 'components/common/button';
import { listPolicyObject } from 'dump/data';
import SearchBar from './SearchBar';
import './style.scss'

const table1 = [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="primary">Chọn</Button>
      </Fragment>
    ),
  },
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Tên đối tượng ',
    dataIndex: 'nameObject',
    key: 'nameObject',
  },
];

const table2 = [

  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Tên đối tượng ',
    dataIndex: 'nameObject',
    key: 'nameObject',
  },
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xóa</Button>
      </Fragment>
    ),
  },
];


class ListPolicyObject extends Component {
  state = {};

  render() {

    return (
      <Fragment>
        <Card title="Chọn đối tượng chính sách">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={listPolicyObject} columns={table1} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sách đối tượng chính sách">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
            <Col span={24}>
              <Table bordered dataSource={listPolicyObject} columns={table2} />
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObject />
  </Page>
);



